import React from "react";

const ServicesCard = ({des,title,img}:any) => {
  return (
    <div className="servicesCard">
      <img src={img} alt="" />
  
      <div className="cardTitle">{title}</div>
      {des&&(
      <div className="cardDes">
      {des}
      </div>
      )}
    </div>
  );
};

export default ServicesCard;
